import React, { useEffect, useContext } from 'react'
import classNames from 'classnames'
import { PDFObject } from 'react-pdfobject'

import { AppDataContext } from '../stores/AppDataContext'

const ResumeSection = ({ show }) => {
  const { isReadyForScroll } = useContext(AppDataContext)
  useEffect(() => {
    if (!isReadyForScroll) return
    document.body.className = show ? 'no-scroll' : ''
  }, [show, isReadyForScroll])

  return (
    <div
      className={classNames('position-fixed top-0 start-0 vw-100 vh-100 z-2', {
        'd-none': !show,
      })}
    >
      <PDFObject
        width="100vw"
        height="100vh"
        url="/playground_assets/resume.pdf"
        forcePDFJS={true}
      />
    </div>
  )
}

export default ResumeSection
