import React, { useState } from 'react'
import { AppDataContext } from '../stores/AppDataContext'
import initialAppData from '../constants/initialAppData'

import { Helmet } from 'react-helmet'
import { ScrollContainer, ScrollPage } from '@ricky-fn/react-scroll-motion'

import './page.css'

import HeroSection from '../sections/hero'
import WorkSection from '../sections/work'
import ContactSection from '../sections/contact'
import ResumeSection from '../sections/Resume'

const Page = () => {
  const [showResumePDF, setShowResumePDF] = useState(false)
  const [appData, setAppData] = useState(initialAppData)

  React.useEffect(() => {
    document.onreadystatechange = () => {
      if (document.readyState === 'complete') {
        clearInterval(window.loading)
        const elem = document.getElementById('abc')
        elem.className += ' complete'

        setTimeout(() => {
          document.getElementById('loader').className += ' d-none'

          setAppData({
            ...appData,
            isContentReady: true,
          })
        }, 500)
      }
    }
  }, [])

  React.useEffect(() => {
    if (!appData.isContentReady) return
    setTimeout(() => {
      setAppData({
        ...appData,
        isReadyForScroll: true,
      })
    }, 2000)
  }, [appData.isContentReady])

  React.useEffect(() => {
    setAppData({
      ...appData,
      showResumePDF,
    })
  }, [showResumePDF])

  return (
    <AppDataContext.Provider value={appData}>
      <div className="page-container">
        <Helmet>
          <title>Ricky - Front End Engineer</title>
        </Helmet>
        <ResumeSection show={showResumePDF} />
        <ScrollContainer>
          <ScrollPage pageHeight={window.innerHeight * 2}>
            <HeroSection showResume={setShowResumePDF} />
          </ScrollPage>
          <ScrollPage pageHeight={window.innerHeight * 4}>
            <WorkSection />
          </ScrollPage>
          <ScrollPage pageHeight={window.innerHeight * 2}>
            <ContactSection showResume={setShowResumePDF} />
          </ScrollPage>
        </ScrollContainer>
      </div>
    </AppDataContext.Provider>
  )
}

export default Page
